body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-bar {
  background-color: rgb(0 0 0 / 51%);
  padding: 0px 5px;
}

.nav-bar .brand-logo {
  font-size: medium;
}

.projectTabs .tab a:focus,
.projectTabs .tab a:focus.active {
  background-color: rgb(0 188 212 / 29%);
}

.projectTabs .tab a:hover,
.projectTabs .tab a.active {
  color: #6ed6eede;
}

.projectTabs .indicator {
  background-color: #b2dbf6;
  color: white;
}

.projectTabs {
  background-color: rgb(0 0 0 / 51%);
}

.projectTabs .tab a {
  color: #ffffffd1;
}

.tabrow {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.tabcol {
  margin: auto;
  max-width: 360px;
}

.projCard {
  background-color: #3520204d;
  color: #fff;
  box-shadow: none;
}

.projCard:hover {
  margin-top: 5px;
  margin-left: -5px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.288);
  transition: all 0.5s ease-in;
}

.projCard::after {
  margin-top: 0px;
  margin-left: 0px;
  box-shadow: none;
  transition: all 0.5s ease-in;
}

.action-link {
  color: #fff !important;
}
.action-link:hover {
  color: #52b8cd !important;
}

body {
  background: #24c6dc; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #514a9d,
    #06a1b6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.bio {
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-content {
  margin: 20px;
  text-align: center;
  color: #fff;
}

.App img {
  width: 200px;
  border-radius: 50%;
  margin-top: 10px;
}

.contact-links i {
  color: white;
  font-size: 3em;
  margin: 0px 20px;
}

p#name {
  font-family: monospace;
  font-size: 35px;
  margin: auto;
}

p#title {
  font-family: monospace;
  font-size: 35px;
  margin: auto;
}

p#skills {
  font-family: monospace;
  font-size: 20px;
  margin: auto;
}

